
<template>
	<div id="main"><!--此处模板会代替index.html的app元素，id可以任意-->
		<transition name="fadeIn">
		<keep-alive>
			<router-view v-if="$route.meta.keepAlive"></router-view><!--路由缓存-->
		</keep-alive>
		</transition>
		<transition name="fadeIn">
		<router-view v-if="!$route.meta.keepAlive"></router-view><!--不需要缓存的-->
		</transition>
		<footMenu></footMenu>
	</div>
</template>

<script>
	import footMenu from "./base/menu.vue";//引用底部菜单基础组件
	export default {
		components:{
			footMenu,
		}
	}

</script>

<style>
/*根组件的样式的公共的，不需要加scoped属性*/
html,body,ul{
	margin:0;
	padding:0;
}
li{
	list-style:none;
}
a{
	text-decoration:none;
}
input,button{/*改变按钮和其他控件的外观*/
	appearance:none;
}
.fadeIn-enter{
	opacity:0;
}
.fadeIn-enter-active{
	transition:all .3s;
}
.fadeIn-leave-active{
	transition:all .3s;
	opacity:0;
}


</style>



