<template>
	<swiper :options="swiperOption" ref="mySwiper">
		<!-- slides -->
		<swiper-slide v-for="(slide,key) in slides" :key="key"><img :src="slide" width="100%" height="auto" /></swiper-slide>
		<!-- Optional controls -->
		<div class="swiper-pagination"  slot="pagination"></div>
	</swiper>
</template>

<script>
	import 'swiper/dist/css/swiper.css';
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	import { getSlides } from "../api";//默认的index.js不用写，使用同源json数据不需要这个
	import axios from "axios";
	export default {
		name: 'carrousel',
		components: {
			swiper,
			swiperSlide
		},
		data() {
			return {
				swiperOption: {
					autoplay : {
						delay:5000
					},
					pagination: {
						el: '.swiper-pagination',
					},
				},
				slides:[],
			}
		},
		computed: {
			swiper() {
				return this.$refs.mySwiper.swiper;
			}
		},
		mounted() {
			this.swiper.slideTo(0, 1000, false);
		},
		//async，await，es7语法，是一对。

		//跨域请求数据，可以封装到方法属性里再到这里调用
		async created(){//vue初始化后调用函数发送url请求，async声明异步操作。
			let { data } = await getSlides();//await 声明等待异步请求完成。解构方式获取对象，冒号后面是重命名对象属性，比如，{data:slides} 将获取到的data 改名为slides。
			this.slides = data;//给组件数据赋值。
		}
		/*async created(){
			await axios.get("./static/slides.json").then(response=>{
				this.slides = response.data;
			} , error=>{
				console.log(error)
			});
		},*/
	}
</script>


