<template>
	<div class="lists">
		<top><span slot="page">列表</span></top>
		<div class="container">
			<h3>图书列表</h3>
			<ul class="books">
				<router-link tag="li" :to="{name:'detail',params:{bid:book.bookId}}" v-for="(book,key) in books" :key="key">
					<img :src="book.bookCover" />
					<h4>{{ book.bookName }}</h4>
					<p>{{ book.bookInfo }}</p>
					<p class="price">{{ "￥" + book.bookPrice }}</p>
					<button class="btn" v-on:click.stop.prevent="Delbook(book.bookId)">删除图书</button>
				</router-link>
				<button class="btn btn-primary" @click="more">加载更多</button>
			</ul>
		</div>
	</div>
</template>
<script>
	import top from "../base/top.vue";//引用头部基础组件
	import { /*getBooks , */delBook , pagination } from "../api";
	export default {
		data(){
			return {
				books:[],
				offset:0,
				hasMore:true,
				isLoading:true
			}
		},
		methods:{
			async Getbooks(){
				if(this.hasMore){
					let { data } = await pagination(this.offset);
					this.books = [...this.books , ...data.books];
					this.hasMore = data.hasMore;
					this.isLoading = false;
					this.offset = this.books.length;
				}
			},
			async Delbook(id){
				await delBook(id);
				//删除前台数据
				this.books = this.books.filter(item=>item.bookId!==id);
			},
			more(){//点击加载
				if(!this.isLoading){
					this.isLoading = true;
					this.Getbooks();
				}
			},
		},
		created(){
			this.Getbooks();
			let that = this;
			window.onscroll = function(){
				clearTimeout(this.timer);
				this.timer = setTimeout(function(){
					if(document.documentElement.offsetHeight - document.documentElement.clientHeight -document.documentElement.scrollTop <= 0){
						that.more();
					}
				},500);
			}
		},
		components:{
			top,
		}
	}
</script>
<style scoped lang="less">
	.container{
		overflow:scroll;
	}
	h3{
		text-align:center;
	}
	.books{
		margin-bottom:50px;
		li{
			cursor:pointer;
			overflow: hidden;
			margin-bottom:20px;
			h4,p{
				padding:0 10px;
			}
			.price{
				color:red;
				font-size:18px;
			}
			img{
				float:left;
				width:200px;
				height:auto;
			}
		}
	}
</style>


