<template>
	<div class="add">
		<top><span slot="page">添加</span></top>
		<div class="content">
			<ul>
				<li>
					<label>图书图片：<input type="text"v-model="book.bookCover" /></label>
				</li>
				<li>
					<label>图书名称：<input type="text"v-model="book.bookName" /></label>
				</li>
				<li>
					<label>图书描述：<input type="text"v-model="book.bookInfo" /></label>
				</li>
				<li>
					<label>图书价格：<input type="text"v-model="book.bookPrice" /></label>
				</li>
				<li>
					<button class="btn btn-primary" @click="Addbook()">确认添加</button>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import top from "../base/top.vue";//引用头部基础组件
	import { addBook } from "../api";
	export default {
		data(){
			return {
				book:{}
			}
		},
		methods:{
			async Addbook(){
				await addBook(this.book);
				this.$router.push("/lists");
			}
		},
		components:{
			top,
		}
	}
</script>
<style scoped lang="less">
	.content{
		margin-bottom:50px;
		ul{
			margin:50px 0;
			padding:0 20px;
			label{
				width:100%;
				margin-bottom:15px;
				font-size:16px;
			}
			input{
				height:35px;
				padding:0 5px;
				width:100%;
				font-size:14px;
			}
		}
	}
</style>


