<template>
	<div class="header">
		<div class="top">
			<slot name="page">标题</slot>
			<i v-if="back" class="iconfont icon-fanhui1" @click="$router.back()"></i>
		</div>
		<div class="slide">
			<swiper></swiper>
		</div>
	</div>
</template>
<script>
	import swiper from "./swiper.vue";
	export default {
		components:{
			swiper,
		},
		props:{
			back:{
				type:Boolean,
				default:true
			}
		},
	}
</script>
<style scoped lang="less">
.top{
	width:100%;
	text-align: center;
	line-height: 30px;
	color:#225599;
	border-bottom:2px solid #225599;
	i{
		display:inline-block;
		font-size:25px;
		position:absolute;
		left:0;
	}
}
</style>


