import Vue from "vue";//导入vue

import router from "./router";//导入自定义的router

import App from "./App.vue";//导入根组件

import "bootstrap/dist/css/bootstrap.min.css";//引用bootstrap样式

new Vue({//实例化Vue对象
	el:"#app",//挂载实例到#app元素
	router,//挂载路由到实例
	template:"<App/>",//使用自定义标签代替index.html的#app元素
	components:{//注册全局组件
		App,
	}
});



