<template>
	<div class="detail">
		<top><span slot="page">详情页</span></top>
		<div class="content">
			<ul>
				<li>
					<label>图书名称：<input type="text"v-model="book.bookName" /></label>
				</li>
				<li>
					<label>图书描述：<input type="text"v-model="book.bookInfo" /></label>
				</li>
				<li>
					<label>图书价格：<input type="text"v-model="book.bookPrice" /></label>
				</li>
				<li>
					<button class="btn btn-primary" @click="Updata()">确认修改</button>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import top from "../base/top.vue";//引用头部基础组件
	import { getBook , upDataBook } from "../api";
	export default {
		components:{
			top,
		},
		data(){
			return {
				book:{}
			}
		},
		computed:{
			bid(){
				return this.$route.params.bid;
			}
		},
		watch:{
			$route(){
				this.Getbook();
			}
		},
		methods:{
			async Getbook(){//获取一本图书
				let { data } = await getBook(this.bid);
				this.book = data;
				//将对象转为数组，检测是否存在，如果不存在，跳转会列表页
				Object.keys(this.book).length>0?void 0 : this.$router.push("/lists");
			},
			async Updata(){//发送修改图书请求
				await upDataBook(this.bid,this.book);//发送请求id和数据（请求体）
				this.$router.push("/lists");
			}
		},
		created(){
			this.Getbook();
		}
	}
</script>
<style scoped lang="less">
	.content{
		margin-bottom:50px;
		ul{
			margin:50px 0;
			padding:0 20px;
			label{
				width:100%;
				margin-bottom:15px;
				font-size:16px;
			}
			input{
				height:35px;
				padding:0 5px;
				width:100%;
				font-size:14px;
			}
		}
	}
</style>


