<template>
	<div class="home">
		<top :back="false"><span slot="page">首页</span></top>
		<loading v-if="loading">加载中……</loading>
		<div v-if="!loading" class="container">
			<h3>热门图书</h3>
			<ul class="hot-book">
				<li v-for="book in hot_book">
					<img :src="book.bookCover" width="100%" height="auto" />
					<p>{{ book.bookName }}</p>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import top from "../base/top.vue";//引用头部基础组件
	import loading from "../base/loading.vue";
	import { getHotBook } from "../api";
	import axios from "axios";//引用axios
	export default {
		data(){
			return {
				hot_book:[],
				loading:true,
			}
		},
		components:{
			top,
			loading
		},
		methods:{
			async Gethotbook(){
				let { data } = await getHotBook();
				this.hot_book = data;
				this.loading = false;
			}
		},
		created(){
			this.Gethotbook();
		}
	}
</script>
<style scoped lang="less">
	.hot-book{
		margin-bottom:50px;
		display:flex;
		flex-wrap:wrap;
		li{
			width:50%;
		}
		p{
			margin:0 0 15px;
			text-align:center;
			padding:0 15px;
			font-size:12px;
			white-space:nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
	.container{
		h3{
			text-align:center;
		}
	}
</style>


